function getExternalFilesPaths(){
  if(window?.getExternalJSPaths){
    var filePaths= window?.getExternalJSPaths();
    if( filePaths && filePaths.length > 0 ){
      for(let fileIndex=0 ; fileIndex < filePaths.length; fileIndex++  ){

        const path = `${process.env.REACT_APP_PUBLIC_URL}${filePaths[fileIndex]}`;

        if(path){
          var script = document.createElement('script');
          script.setAttribute('src', path);
          script.setAttribute('id', `iformClient_js_appformBuilder${fileIndex}`);
          document.body.appendChild(script);
        }
      }
    }
  }
}

function loadIformClient(){
  const isIformClientLoaded = document.querySelector(`script[src="${process.env.REACT_APP_PUBLIC_URL}/iformclient.js"]`);
  if(isIformClientLoaded===null){
    const iformClient_url= `${process.env.REACT_APP_PUBLIC_URL}/iformclient.js`;
    var script = document.createElement('script');
    script.setAttribute('src', iformClient_url);
    script.setAttribute('id', "iformClient_js_appformBuilder");
    script.onload= () =>{ 
      getExternalFilesPaths();
    }
    document.body.appendChild(script);
  }
}

const localeKey=secureLocalStorage.getItem("locale");
    if(localeKey){
      localStorage.setItem("locale",localeKey);
      window.appformBuilder_configs.selectedLanguage= localeKey || navigator.language || navigator.userLanguage;
    }
      
const translationStyles1=window?.appformBuilder_configs?.selectedLanguage;
  if(process.env.REACT_APP_PUBLIC_URL === "/appformBuilder"){
    loadIformClient();
  }



